import React, {Component} from 'react';
import PropTypes from 'prop-types';
import _ from "lodash";
import RGL, { WidthProvider } from "react-grid-layout";
import "../assets/dragdash.css"

const ReactGridLayout = WidthProvider(RGL);
/**
 * GridComponent is an dash component.
 * It takes a property, `id`, and children to
 * displays it.
 */
export default class DashGrid extends Component {

      constructor(props) {
        super(props);

        const layout = this.generateLayout();
        this.state = { 
           layout
           };
        this.onLayoutChange = this.onLayoutChange.bind(this);
      }


      generateLayout() {
        const p = this.props;
        console.log("count",React.Children.count(this.props.children))
        return _.map(new Array(React.Children.count(this.props.children)), function(item, i) {
          const y = _.result(p, "y") || Math.ceil(Math.random() * 4) + 1;
          console.log("p",p)
          console.log("item",item)
          return {
            x: (i * 2) % 12,
            y: Math.floor(i / 6) * y,
            w: 2,
            h: y,
            i: i.toString()
          };
        });
      }

      onLayoutChange(layout) {
        console.log("layout",layout)
        //saveToLS("layout", layout);
        console.log(this.props.position)
        this.setState({ layout : layout });
        //this.props.onLayoutChange(layout);
      }

      setPositiononLoad( ){
        this.setState({ layout : this.props.position });
      }

        stringifyLayout() {
          //console.log("stringify",this.state.layout)
          return this.state.layout.map(function(l) {
            const name = l.i === "__dropping-elem__" ? "drop" : l.i;
            return (
              <div className="layoutItem" key={l.i}>
                <b>{name}</b>
                {`: [${l.x}, ${l.y}, ${l.w}, ${l.h}]`}
              </div>
            );
          });
        }

    render() {
        const {id, children, position, setProps } = this.props;
        //console.log("this.state",this.state)
        //console.log("this.state",this.props)
        //console.log("this.state.layout",this.state.layout)
        //console.log("this.stringifyLayout()",this.stringifyLayout())
        return (
            <div id={id}>
                  <div>
                      <div className="layoutJSON">
                        Displayed as <code>[x, y, w, h]</code>:
                        <div className="columns">{this.stringifyLayout()}</div>
                      </div>
                  </div>
                    <ReactGridLayout
                        {...this.props}
                        layout={this.state.layout}
                        onLayoutChange={this.onLayoutChange}     
                      >
                      {this.props.children.map((child, index) => (<div key={index}>{child}</div>))}
                    </ReactGridLayout>
            </div>
        );
    }
}

DashGrid.defaultProps = {
    className: "layout",
    items: 50,
    cols: 12,
    rowHeight: 30,
    // This turns off compaction so you can place items wherever.
    verticalCompact: false,
    // This turns off rearrangement so items will not be pushed arround.
    preventCollision: true
};

DashGrid.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,

    /**
     * The children components displayed inside the grid.
     */
    children : PropTypes.node,

    /**
     * The layout of the  components displayed inside the grid.
     */
    position : PropTypes.array,
    
    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func
};
